				console.log('newdata')
				console.log(this.bj_prodata)
				console.log(newdata)
				var edit_idx = this.bj_prodata.idx
				Vue.set(that.datas, edit_idx, newdata)
				// that.datas[edit_idx]=newdata
				console.log(that.datas)
				this.edit_problem('')
			}
		},
		computed: {
			...mapState(['new_problem', 'bj_prodata', 'loginDatas']),
		},
		methods: {
			...mapMutations(['setnew_problem', 'edit_problem', 'setls_prodata', 'setls_pro_yh', 'set_h5_uid']),
			set_op1(e){
				var ans_idx = e.detail.value
				this.qiyong_index=e.detail.value
			},
			set_op(e) {
				console.log(e.detail.value)
				console.log(e.currentTarget.dataset.idx)
				var ans_idx = e.detail.value
				var idx = e.currentTarget.dataset.idx
				Vue.set(that.guding[idx], 'option', that.guding[idx].answer[ans_idx].option)
				Vue.set(that.guding[idx], 'option_text', that.guding[idx].answer[ans_idx].title)
			},
			addcg() {
				var data = {
					dy_title: that.dy_title,
					dy_explain: that.dy_explain,
					dy_addition_explain: that.dy_addition_explain,
					datas: that.datas
				}
				var datas = {
					// token: that.loginDatas.userToken || '',
					// #ifdef MP-WEIXIN
					token:that.loginDatas.userToken || '',
					// #endif
					// #ifndef MP-WEIXIN
					user_id:that.uid,
					// #endif
					data: JSON.stringify(data)
				}
				var jkurl = '/user/draft/add'
				// #ifdef H5
				// console.log(service.adminurl)
				// jkurl =service.adminurl+'/research_papers.ResearchPapers/saveResearch'
				var adminurl=service.IPurl0+'admin/'
				jkurl=adminurl+'/research_papers.ResearchPapers/draftsSave'
				// #endif
				if (that.id) {
					datas = {
						id: that.id,
						// #ifdef MP-WEIXIN
						token:that.loginDatas.userToken || '',
						// #endif
						// #ifndef MP-WEIXIN
						user_id:that.uid,
						// #endif
						data: JSON.stringify(data)
					}
					jkurl = '/user/draft/modify'
					// #ifdef H5
					// console.log(service.adminurl)
					// jkurl =service.adminurl+'/research_papers.ResearchPapers/saveResearch'
					var adminurl=service.IPurl0+'admin/'
					jkurl=adminurl+'/research_papers.ResearchPapers/draftsUpdate'
					// #endif
				}
				service.P_post(jkurl, datas).then(res => {
					that.btn_kg = 0
					console.log(res)
					if (res.code == 1) {
						var datas = res.data
						console.log(typeof datas)

						if (typeof datas == 'string') {
							datas = JSON.parse(datas)
						}
						uni.showToast({
							icon: 'none',
							title: '操作成功'
						})


					} else {
						if (res.msg) {
							uni.showToast({
								icon: 'none',
								title: res.msg
							})
						} else {
							uni.showToast({
								icon: 'none',
								title: '操作失败'
							})
						}
					}
				}).catch(e => {
					that.btn_kg = 0
					console.log(e)
					uni.showToast({
						icon: 'none',
						title: '获取数据失败'
					})
				})
			},
			getcg() {
				var datas = {
					// #ifdef MP-WEIXIN
					token:that.loginDatas.userToken || '',
					// #endif
					// #ifndef MP-WEIXIN
					user_id:that.uid,
					// #endif
					id: that.id
				}

				//selectSaraylDetailByUserCard
				var jkurl = '/user/draft/look'
				// #ifdef H5
				// console.log(service.adminurl)
				// jkurl =service.adminurl+'/research_papers.ResearchPapers/saveResearch'
				var adminurl=service.IPurl0+'admin/'
				jkurl=adminurl+'/research_papers.ResearchPapers/draftsLook'
				// #endif
				uni.showLoading({
					title: '正在获取数据'
				})
				service.P_get(jkurl, datas).then(res => {
					that.btn_kg = 0
					console.log(res)
					if (res.code == 1) {
						var datas = res.data
						console.log(typeof datas)

						if (typeof datas == 'string') {
							datas = JSON.parse(datas)
						}
						that.dy_title = datas.data.dy_title
						that.dy_explain = datas.data.dy_explain
						that.dy_addition_explain = datas.data.dy_addition_explain
						that.datas = datas.data.datas
						console.log(datas)


					} else {
						if (res.msg) {
							uni.showToast({
								icon: 'none',
								title: res.msg
							})
						} else {
							uni.showToast({
								icon: 'none',
								title: '操作失败'
							})
						}
					}
				}).catch(e => {
					that.btn_kg = 0
					console.log(e)
					uni.showToast({
						icon: 'none',
						title: '获取数据失败'
					})
				})
			},
			getleft_dian(item) {
				var jieguo = item.jieguo - item.answer.min_num
				var zum = item.answer.max_num - item.answer.min_num
				var leftnum = jieguo * 1 / zum * 1
				console.log(leftnum)
				// var leftnum='left:'+leftnum+';'
			},
			get_hd(min_num, max_num) {
				// var arr=[
				// 	min_num,
				// 	min_num,
				// 	min_num,
				// 	min_num,
				// 	min_num,
				// 	max_num,
				// ]
				// arr[1]=((max_num-min_num)*0.2 +min_num*1).toFixed(2)
				// arr[2]=((max_num-min_num)*0.4+min_num*1).toFixed(2)
				// arr[3]=((max_num-min_num)*0.6+min_num*1).toFixed(2)
				// arr[4]=((max_num-min_num)*0.8+min_num*1).toFixed(2)
				var arr = []
				for (var i = min_num; i <= max_num; i++) {
					arr.push(i)
				}
				return arr
			},
			sliderChange(e) {
				var that = this
				console.log(e)
				var idxs = e.currentTarget.dataset.idx
				Vue.set(that.datas[idxs], 'jleft1', e.detail.value)
				var jleft1 = e.detail.value
				var item = that.datas[idxs]

				var jieguo = jleft1 - item.answer.min_num
				console.log(jieguo)
				var zum = item.answer.max_num - item.answer.min_num
				console.log(zum)
				var leftnum = jieguo / zum
				console.log(leftnum)
				leftnum = leftnum * 100
				leftnum = leftnum + '%'
				Vue.set(that.datas[idxs], 'jleft', leftnum)
				console.log(that.datas[idxs])
			},
			px_up(item, idx) {
				if (idx == 0) {
					return
				}
				var that = this
				// var newarr=item.answer
				var temp = JSON.parse(JSON.stringify(item));
				// newarr[idx] =newarr[idx-1];
				// newarr[idx-1] = temp;
				console.log('px_up')
				var idx_1 = idx * 1 - 1
				Vue.set(that.datas, idx, that.datas[idx - 1])
				Vue.set(that.datas, idx_1, temp)

			},
			px_down(item, idx) {
				if (idx == this.datas.length - 1) {
					return
				}
				var that = this
				// var newarr=item.answer
				var temp = JSON.parse(JSON.stringify(item));
				// newarr[idx] =newarr[idx+1];
				// newarr[idx+1] = temp;

				// console.log('datas')
				// console.log(item)
				// console.log(idx)
				console.log('px_down')
				var idx_1 = idx * 1 + 1
				Vue.set(that.datas, idx, that.datas[idx + 1])
				Vue.set(that.datas, idx_1, temp)

			},
			pro_edit(item, idx) {
				var that = this
				console.log(item.type)
				var edit_data = {
					idx: idx,
					datas: item,
				}
				that.edit_problem(edit_data)
				if (item.type == 3) {
					uni.navigateTo({
						url: '/pages/fabu_tiankong/fabu_tiankong?type=3'
					})
				} else if (item.type == 5) {
					uni.navigateTo({
						url: '/pages/fabu_huadong/fabu_huadong?type=5'
					})
				} else {
					uni.navigateTo({
						url: '/pages/fabu_duoxuan/fabu_duoxuan?type=' + item.type
					})
				}
			},
			pro_del(idx) {
				var that = this
				uni.showModal({
					title: '提示',
					content: '是否确认删除此题？',
					success: function(res) {
						if (res.confirm) {
							console.log('用户点击确定');
							uni.showToast({
								icon: 'none',
								title: '操作成功'
							})
							that.datas.splice(idx, 1)
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				})
			},
			sub() {
				var that = this
				console.log(this.datas)
				if (!that.dy_title) {
					uni.showToast({
						icon: 'none',
						title: '请输入调研标题'
					})
					return
				}
				// if(!that.dy_explain){
				// 	uni.showToast({
				// 		icon:'none',
				// 		title:'请输入调研说明'
				// 	})
				// 	return
				// }	
				// if(!that.dy_addition_explain){
				// 	uni.showToast({
				// 		icon:'none',
				// 		title:'请输入调研附加说明'
				// 	})
				// 	return
				// }	
				if (that.datas.length == 0) {
					uni.showToast({
						icon: 'none',
						title: '请添加问题'
					})
					return
				}
				var ls_data = {
					dy_title: that.dy_title,
					dy_explain: that.dy_explain,
					dy_addition_explain: that.dy_addition_explain,
					datas: that.datas,
				}
				this.setls_prodata(ls_data)
				uni.showModal({
					title: '请仔细确认发布内容，发布后不可修改',
					success: function(res) {
						if (res.confirm) {
							console.log('用户点击确定');
							// #ifdef MP-WEIXIN
							uni.navigateTo({
								url: '../fabu2/fabu2?id=' + that.id+'&qiyong_index='+that.qiyong_index
							})
							// #endif
							// #ifdef H5
							uni.navigateTo({
								url: '../fabu3/fabu3?uid=' + that.uid + '&uids=' + that.uids + '&id=' +
									that.id+'&qiyong_index='+that.qiyong_index
							})
							// #endif
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				})

			},
			jump(e) {
				service.jump(e)
			},
			getimg(img) {
				return service.getimg(img)
			},
			pveimg(e) {
				service.pveimg(e)
			}
		}
	}
</script>

